#nav {
    display: flex;
    background-color: white;
    justify-content: space-around;
    width: 50%;
    height: 100px;
    ;

}


.nav_content {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 2px solid #792e2e ;
}


.not_clicked {
    background-color: white;
    border: none;
    /* color: white; */
    padding: 20px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    /* margin: 4px 2px; */
    cursor: pointer;
    /* text-transform: uppercase; */


}

.not_clicked:hover {
    border-top: 2px solid #ffb703;

}

.not_clicked:active {
    text-decoration: none;
    color: black
}

.not_clicked:visited {
    text-decoration: none;
    color: black
}

.not_clicked:focus {
    background-color: #ffb703;
    color: white;
    border: none;
    /* color: white; */
    /* padding: 10px 20px; */
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    /* margin: 4px 2px; */
    cursor: pointer;
    /* border:1px solid black; */
}

.btn_change {
    border: none;
    background-color: none;
    box-shadow: none;
    color: black;
}


