.togged_kafedra,
.togged_bagytary {
    display: grid;
    grid-template-columns: 1fr 3fr;
    padding: 20px;
    align-items: center;
    justify-content: space-between;
    line-height: 30px;
    text-transform: uppercase;
}

.togged_kafedra .part1 {
    width: 80px;
    color: #FB8500;
    border-bottom: 1px solid;
    border-color: #FB8500;
}

.togged_bagytary .part1 {
    width: 150px;
    color: #FB8500;
    border-bottom: 1px solid;
    border-color: #FB8500;
}

.try {
    height: 200px;
}

ul li {
    list-style: none;
}

ul li a {
    text-decoration: none;
    color: #333;

}

.part2 {
    display: flex;
    flex-wrap: wrap;
}

.column {
    flex-basis: calc(50% - 1em);
    /* for two columns */
    /* or */
    flex-basis: calc(33.33% - 1em);
    /* for three columns */
    margin-right: 1em;
    border-left: 3px solid #FB8500;
    padding-left: 10px;
}

.search {
    width: 100%;
    height: 110px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.search-bar {
    width: 80%;
    max-width: 500px;
    background: rgb(255, 255, 255, 0, 2);
    display: flex;
    align-items: center;
    border-radius: 60px;
    padding: 10px 0px 10px 20px;
    backdrop-filter: blur(4px)satur;
    border: 3px solid rgb(55, 6, 52);
    height: 40%;
    margin-bottom: 30px;


}

.search-bar input {
    background: transparent;
    flex: 1;
    border: 0;
    outline: none;
    font-size: 20px;
    color: #cac7ff;
}

::placeholder {
    color: #792e2e;
}

.search-bar button img {
    width: 25px;

}

.search-bar button {
    border: 0;
    border-radius: 100px;
    width: 60px;
    height: 50%;
    height: 40px;
    background: #ffb703;
    color: black;
    cursor: pointer;
}

